import React, { Component } from "react";
import { Document, Page } from "react-pdf";
import screen from "../../utils/something.js";
import resume from "../../../content/TFarris_Resume_Jan2019Ex.pdf";
class Pdf extends Component {
  state = {
    numPages: null,
    pageNumber: 1
  };

  onDocumentLoadSuccess = ({ numPages }) => {
    this.setState({ numPages });
  };

  render() {
    const { pageNumber, numPages } = this.state;

    return (
      <div>
        <Document renderMode='svg' file={resume} onLoadSuccess={this.onDocumentLoadSuccess}>
          {Array.from(new Array(numPages), (el, index) => (
            <Page width={((screen.width > 600)? screen.width/2 : screen.width)} key={`page_${index + 1}`} pageNumber={index + 1} />
          ))}
        </Document>
      </div>
    );
  }
}

export default Pdf;
